ACA,aAAA;EACA,WAAA;EACA,mBAAA;EACA,oBAAA;EACA,kBAAA;EACA,oBAAA;EACA,WAAA;EACA,YAAA;EACA,mBAAA;CX2hCL;AWxhCG;EAEI,OAAA;CXyhCP;AWrhCG;EAEI,UAAA;EACA,cAAA;CXshCP;AWnhCK;EAEI,UAAA;EACA,UAAA;CXohCT;AWhkCD;EAkDM,mBAAA;EACA,WAAA;EACA,gBAAA;EACA,kBAAA;EACA,aAAA;EACA,SAAA;CXihCL;AW9gCC;EAEI,mEAAA;CX+gCL","file":"redaxtor.less","sourcesContent":["@import (reference) \"vars\";\n\n.r_reset {\n  all: initial;\n\n  /*\n      HTML5 Reset :: style.css\n      ----------------------------------------------------------\n      We have learned much from/been inspired by/taken code where offered from:\n\n      Eric Meyer\t\t\t\t\t:: http://meyerweb.com\n      HTML5 Doctor\t\t\t\t:: http://html5doctor.com\n      and the HTML5 Boilerplate\t:: http://html5boilerplate.com\n\n  -------------------------------------------------------------------------------*/\n\n  /* Let's default this puppy out\n  -------------------------------------------------------------------------------*/\n\n  html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary {\n    margin: 0;\n    padding: 0;\n    border: 0;\n    font-size: 100%;\n    font-weight: normal;\n    vertical-align: baseline;\n    background: transparent;\n  }\n\n  main, article, aside, figure, footer, header, nav, section, details, summary {display: block;}\n\n  /* Handle box-sizing while better addressing child elements:\n     http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */\n  html {\n    box-sizing: border-box;\n  }\n\n  *,\n  *:before,\n  *:after {\n    box-sizing: inherit;\n  }\n\n  /* consider resetting the default cursor: https://gist.github.com/murtaugh/5247154 */\n\n  /* Responsive images and other embedded objects */\n  /* if you don't have full control over `img` tags (if you have to overcome attributes), consider adding height: auto */\n  img,\n  object,\n  embed {max-width: 100%;}\n\n  /*\n     Note: keeping IMG here will cause problems if you're using foreground images as sprites.\n      In fact, it *will* cause problems with Google Maps' controls at small size.\n      If this is the case for you, try uncommenting the following:\n\n  #map img {\n          max-width: none;\n  }\n  */\n\n  /* force a vertical scrollbar to prevent a jumpy page */\n  html {overflow-y: scroll;}\n\n  /* we use a lot of ULs that aren't bulleted.\n      you'll have to restore the bullets within content,\n      which is fine because they're probably customized anyway */\n  ul {list-style: none;}\n\n  blockquote, q {quotes: none;}\n\n  blockquote:before,\n  blockquote:after,\n  q:before,\n  q:after {content: ''; content: none;}\n\n  a {margin: 0; padding: 0; font-size: 100%; vertical-align: baseline; background: transparent;}\n\n  del {text-decoration: line-through;}\n\n  abbr[title], dfn[title] {border-bottom: 1px dotted #000; cursor: help;}\n\n  /* tables still need cellspacing=\"0\" in the markup */\n  table {border-collapse: separate; border-spacing: 0;}\n  th {font-weight: bold; vertical-align: bottom;}\n  td {font-weight: normal; vertical-align: top;}\n\n  hr {display: block; height: 1px; border: 0; border-top: 1px solid #ccc; margin: 1em 0; padding: 0;}\n\n  input, select {vertical-align: middle;}\n\n  pre {\n    white-space: pre; /* CSS2 */\n    white-space: pre-wrap; /* CSS 2.1 */\n    white-space: pre-line; /* CSS 3 (and 2.1 as well, actually) */\n    word-wrap: break-word; /* IE */\n  }\n\n  input[type=\"radio\"] {vertical-align: text-bottom;}\n  input[type=\"checkbox\"] {vertical-align: bottom;}\n  .ie7 input[type=\"checkbox\"] {vertical-align: baseline;}\n  .ie6 input {vertical-align: text-bottom;}\n\n  select, input, textarea {font: 99% sans-serif;}\n\n  table {font-size: inherit; font: 100%;}\n\n  small {font-size: 85%;}\n\n  strong {font-weight: bold;}\n\n  td, td img {vertical-align: top;}\n\n  /* Make sure sup and sub don't mess with your line-heights http://gist.github.com/413930 */\n  sub, sup {font-size: 75%; line-height: 0; position: relative;}\n  sup {top: -0.5em;}\n  sub {bottom: -0.25em;}\n\n  /* standardize any monospaced elements */\n  pre, code, kbd, samp {font-family: monospace, sans-serif;}\n\n  /* hand cursor on clickable elements */\n  .clickable,\n  label,\n  input[type=button],\n  input[type=submit],\n  input[type=file],\n  button {cursor: pointer;}\n\n  /* Webkit browsers add a 2px margin outside the chrome of form elements */\n  button, input, select, textarea {margin: 0;}\n\n  /* make buttons play nice in IE */\n  button,\n  input[type=button] {width: auto; overflow: visible;}\n\n  /* let's clear some floats */\n  .clearfix:after { content: \" \"; display: block; clear: both; }\n\n}\n\n/* REDAXTOR Specific reset styles now */\n.r_reset {\n  font-family: @rx-font; // Setup base font for everything\n  font-size: 12px; // Base font size\n  color: #444;\n}",".r_reset {\n  all: initial;\n  /*\n      HTML5 Reset :: style.css\n      ----------------------------------------------------------\n      We have learned much from/been inspired by/taken code where offered from:\n\n      Eric Meyer\t\t\t\t\t:: http://meyerweb.com\n      HTML5 Doctor\t\t\t\t:: http://html5doctor.com\n      and the HTML5 Boilerplate\t:: http://html5boilerplate.com\n\n  -------------------------------------------------------------------------------*/\n  /* Let's default this puppy out\n  -------------------------------------------------------------------------------*/\n  /* Handle box-sizing while better addressing child elements:\n     http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */\n  /* consider resetting the default cursor: https://gist.github.com/murtaugh/5247154 */\n  /*